

button{
  flex-wrap: wrap;
  background-color:rgba(243, 243, 70, 0.829);
  padding: 50px;
  justify-content: center;
  display: flex;
  margin: 20px;
  font-size: 16px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: bold;
  color: navy;
  width: 300px;
  height: 500px;
  border-radius: 30px;
  -webkit-box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92); 
  box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92);
}

button img{
  border-radius: 20px;
}


button:hover {
  background-color: #eb8371;
 
 
 
}


button img{
  display: block;
  width: 200px;
}

.movieStyleDiv{
  display: flex;
  background-color: rgba(10, 5, 75, 0.685);
  min-width:70%;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction: row;
  text-align: center;
  padding: 6%;
}

.displayMovie {
  text-align: center;
  background-color: #eb8371;
  height: 500px;
  font-size: 18px;
  padding:5%;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: navy;
  border-radius: 30px;
  margin: 8% 20%;
  font-weight: bold;
  -webkit-box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92); 
  box-shadow: 4px 7px 12px -5px rgba(0,0,0,0.92);

}

.displayMovie img{
  width: 200px;
  border-radius: 20px;
}


.Title {
  margin: 10px 40%;
  font-size: 27px;
  font-weight: bold;
}

.displayMovie:hover{
  background-color:rgba(243, 243, 70, 0.829);
}

button p{
  font-size: 15px;
  margin:20px;
}
